.Menu{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
    z-index: 100;
}

.Button{
    padding: 40px 80px;
    background-color: #3c408c;
    font-family: PublicPixel;
    font-size: 2em;
    color: #885ca4;;
    border-radius: 20px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 60px;
    cursor: pointer;
}
