.blogItem-wrap footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 1rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.blogItem-link {
  text-decoration: none;
  color: #46023d;
  font-weight: bold;
  font-size: .8rem;
}

.blogItem-author {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.blogItem-author img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0.5rem;
}

.blogItem-author p {
  font-size: 0.7rem;
  color: #46023d;
  font-weight: 600;
}

.blogItem-wrap h6 {
  margin: 0.5rem 0 1rem 0;
  -webkit-flex: 1 1;
          flex: 1 1;
  color: #46023d;
  font-size: 0.7rem;
}
.blogList-wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
}

@media (max-width: 768px) {
  .blogList-wrap {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .blogList-wrap {
    grid-template-columns: repeat(1, 1fr);
  }
}

